fs14);\n    line-height: var(--lh6);\n    background-color: var(--banner--bgc);\n    border-radius: 0;\n    box-shadow: none;\n\n    // If you want to hide and reveal the banner\n    &[aria-hidden='true'] {\n        --topbar-height: var(--sun64);\n\n        visibility: hidden;\n        opacity: 0;\n    }\n\n    &[aria-hidden='false'] {\n        visibility: visible;\n        opacity: 1;\n    }\n\n    //  If you want to put the banner above the topbar\n    &.d-banner--pinned {\n        z-index: calc(~'var(--zi-navigation-fixed) + 1');\n        transform: translate3d(0, 0, 0);\n    }\n}\n\n.d-banner--transform {\n    transform: translate3d(0, var(--topbar-height), 0);\n}\n\n//  ============================================================================\n//  $   CONTENT CONTAINER\n//  ----------------------------------------------------------------------------\n.d-banner__dialog {\n    position: relative;\n    display: flex;\n    align-items: center;\n    width: 100%;\n    max-width: 128rem;\n    min-height: 100%;\n    margin: 0 auto;\n    padding: var(--su8);\n\n    .d-notice__content {\n        flex-direction: row;\n        align-items: center;\n\n        .d-flow4();\n    }\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-banner.d-banner--important {\n    --banner--bgc: var(--black-800);\n    --banner--fc: var(--black-025);\n}\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-banner--error {\n    --banner--bgc: var(--red-100);\n    --banner--fc: var(--red-700);\n\n    &.d-banner--important {\n        --banner--bgc: var(--red-500);\n        --banner--fc: var(--white);\n    }\n}\n\n//  $$  INFO\n//  --------------------------------------------------------------------------